// root
:root {
  $--font-main: "ApercuProMono";
  font: size 16px $--font-main;

  line-height: 16px;
  font-smooth: auto;
  font-smooth: never;
  font-smooth: always;
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-smoothing: antialiased;

  $--color-rescue: #7d8a94;

  box-sizing: border-box;

  overflow: hidden;
  scroll-behavior: smooth;

  cursor: none;
}

// text line height
$--line-height-simple: 1rem;
$--line-height-quart: 1.25rem;
$--line-height-double: 2rem;
$--line-height-triple: 3rem;
$--line-height-quad: 4rem;

// fix text word spacing
$--word-spacing: -0.1rem;

// margin
$--margin-0: 0em;
$--margin-half: 0.5rem;
$--margin-simple: 1rem;
$--margin-double: 2rem;
$--margin-triple: 3rem;
$--margin-quad: 4rem;

// padding
$--padding-0: 0rem;
$--padding-half: 0.5rem;
$--padding-34: 0.75rem;

$--padding-simple: 1rem;
$--padding-double: 2rem;
$--padding-triple: 3rem;
$--padding-quad: 4rem;

// main colors
$--color-purple: #5114de;
$--color-pink: #ffe0dc;
$--color-green: #7d938a;
$--color-orange: #f0611e;
$--color-yellow: #edc016;

$--color-etiquette-1: #c6b7b5;
$--color-etiquette-2: #c6b7b5;
$--color-etiquette-3: #c6b7b5;
$--color-etiquette-4: #c6b7b5;
$--color-etiquette-5: #c6b7b5;

$--color-filtre-1: #00000082;
$--color-filtre-2: rgba(28, 27, 50, 1);
$--color-filtre-3: rgb(50, 42, 27);
$--color-filtre-4: rgba(0, 0, 0, 0.727);
$--color-filtre-5: #26263466;
$--color-filtre-6: #1b2a2c;

// border-color
$--border-background: #cbcbcb;

// border-radius
$--border-0: 0rem;
$--border-1: 1rem;
$--border-1_5: 1.5rem;
$--border-2: 2rem;
$--border-3: 3rem;

//font
$--font-main: "ApercuProMono";
$--font-menu: "TIMMONS NY 2.0";

$--font-size-1: 1rem;
$--font-size-2: 2rem;
$--font-size-4: 4rem;
$--font-size-8: 8rem;


$--color-background: #f4f2ed;
//$--color-background: #dad3ce;

html, body{
  height:100%;
  overflow-y: scroll;
}

html.has-scroll-smooth {
  overflow: hidden;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}

// body
body {
  background: $--color-background;
  overflow-x: hidden;
  background-attachment: fixed;
  overflow-anchor: [auto];
}

