.app_container{
  position: relative;
  bottom:0px;

  width:100vw;
  height:750px;
  width:100svw;

  box-sizing: border-box;
  padding:2rem;

  display: flex;
  justify-content: center;
  align-items: center;
  
  background: linear-gradient(170deg, #7d938a 30%, #949d99 70%);
  overflow: hidden;

}
.app_jumbo{
  box-sizing: border-box;
  background-color: #ffffff00;
  width: 100%;
  height: 25%;
  display: block;
  position: absolute;
  top: 0px;
  padding-top:5rem;
  padding-left:5rem;
  padding-right:5rem;
  z-index:20;
}

.jumbo_text{
  color: #ebe9e7;
  margin-left: 5rem;
  padding-bottom: 1rem;
  font-family: $--font-menu;
  font-size: 10rem;
}
.jumbo_line{

  background-color: #242431;
  width: 70%;
  height: .1px;
  position: relative;
  bottom: 0;
}

.app_body{
  box-sizing: border-box;
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  width: 100%;
  height: 75%;
  position: absolute;
  padding-left: 5rem;
  padding-right: 5rem;
  padding-bottom: 5rem;
  bottom: 0px;
  overflow:hidden;

}

.body_meth{
  position: relative;
  left:1px;
  border: .1px solid #24243169;
  border-radius: 30px 0px 0px 30px;
  flex-direction: column;
  justify-content: center;
  width: 70%;
  height: 100%;
  padding: 2rem;
  box-sizing: border-box;
  display: flex;
}

.marginplus{
  margin-top:2rem;
}

.body_soft{
//background: linear-gradient(344deg, #6e6e77 -50%, #242431 70%);
background: url(../../svg_apps/bg.svg);
filter: invert(1);
background-size: cover;
box-shadow: inset 0 0 1rem #4c4c4c96;
border: 1px solid #4d4d58ab;
  width: 30%;
  height: 100%;
  border-bottom-right-radius: 2rem;
  border-top-right-radius: 2rem;
  display: flex;
  justify-content: center;
  flex-direction: column;
  overflow: hidden;
}
.body_text{
  font-family: $--font-main;
  font-size: 2.5rem;
  color:#4a5159;
  font-family: ApercuProMono;
  height: 2rem;
  padding: 0;
  opacity: 0;
  animation: bodyopa .6s .3s ease-out;
  animation-play-state: paused;
}

.body_text2{
  opacity: 0;
  width:100%;
  margin: .5rem 0;
  padding-left: 4rem;
  font-family: 'ApercuPro';
  font-size: 1rem;
  color: #e2e2e2;
  animation: bodyopa 1s .3s ease-out;
  animation-play-state: paused;

}

.body_line{
  background: #d0d2d38f;
  display: flex;
  position: relative;
  left: 0px;
  top: .3rem;
  height:.5px;
  width: 0%;
  margin-left:4rem;
  overflow: hidden;
  animation: bodywidth 1s .5s cubic-bezier(0.92, 0.52, 0.21, 0.89);
  animation-play-state: paused;
}

.body_anim{
  animation-play-state: running;
  animation-iteration-count: 1;
  animation-fill-mode: forwards;
}

@keyframes bodyopa {
  100%{ opacity: 1;}
 };

@keyframes bodywidth {
  100%{ width: calc(100% - 4rem);}
 };

