.hover-contact-underline:after{ 
  content: "";    
  opacity: 0;
	position: absolute;
	bottom: 1rem;
	display: block;
	height: 2px;
	width: 100%; 
  background-image: linear-gradient( #cf4721, #d82d37);
  background-position: 0% 100%;
  background-repeat: no-repeat;
  background-size: 100% 0px;
  background-origin: border-box;
  transition: background-size .2s cubic-bezier(.45, .34, .65, .96), opacity .5s ease-out;
  display: inline-block;}

.hover-contact-underline:focus, .hover-contact-underline:hover::after {
background-position: 100% 100%;
background-size: 100% 1.5px;
opacity:1;}
